/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0; }

/**
 * Basic styling
 */
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-size: calc(14px + .2vw + .2vh);
  line-height: 1.56;
  font-weight: 400;
  letter-spacing: .01rem;
  color: #2a2a2a;
  background-color: #fafafa;
  -webkit-text-size-adjust: 100%;
  -o-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  font-kerning: normal; }

/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
.highlight {
  margin-bottom: 1rem; }

/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle; }

/**
 * Figures
 */
figure > img {
  display: block; }

figcaption {
  font-size: 15.75px; }

/**
 * Lists
 */
ul, ol {
  margin-left: 2rem; }

li > ul,
li > ol {
  margin-bottom: 0; }

/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

/**
 * Links
 */
a {
  color: inherit;
  text-decoration: none; }
  a:visited {
    color: inherit; }
  a:hover {
    color: inherit;
    text-decoration: underline; }

/**
 * Blockquotes
 */
blockquote {
  color: #828282;
  border-left: 4px solid #cdcdcd;
  padding-left: 1rem;
  font-size: 18px;
  letter-spacing: -1px;
  font-style: italic; }
  blockquote > :last-child {
    margin-bottom: 0; }

/**
 * Code formatting
 */
pre,
code {
  font-size: 15px;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  background-color: #eef; }

code {
  padding: 1px 5px; }

pre {
  padding: 8px 12px;
  overflow-x: auto; }
  pre > code {
    border: 0;
    padding-right: 0;
    padding-left: 0; }

/**
 * Wrapper
 */
.wrapper-home {
  position: relative;
  top: 117px; }

.wrapper-home {
  width: 54vw;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto; }
  @media screen and (min-width: 1024px) {
    .wrapper-home {
      width: 84vw;
      max-width: calc(1080px - (2rem * 2)); } }

.wrapper {
  width: 54vw;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto; }
  @media screen and (min-width: 1024px) {
    .wrapper {
      width: 48vw;
      max-width: calc(1080px - (2rem * 2)); } }

@media screen and (max-width: 800px) {
  .wrapper {
    width: 60vw;
    max-width: calc(1080px - (2rem * 2)); } }

@media screen and (max-width: 600px) {
  .wrapper {
    width: calc(100% - 2rem);
    max-width: calc(600px - (2rem * 2)); } }

/**
 * Site header
 */
.site-header-home {
  padding-top: 7vh;
  border-bottom: 20px solid #fafafa;
  overflow: hidden; }

.site-title,
.home-logo {
  width: 100%; }

/**
 * Clearfix
 */
.wrapper-home:after, .wrapper:after, .footer-col-wrapper:after {
  content: "";
  display: table;
  clear: both; }

/**
 * Icons
 */
.icon > svg {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: middle; }
  .icon > svg path {
    fill: #828282; }

html,
body {
  min-height: 100%; }

::-moz-selection {
  background: #FFEB3B;
  color: #1d1d1d; }

::selection {
  background: #FFEB3B;
  color: #1d1d1d; }

/**
 * Site header
 */
.site-header {
  min-height: 56px;
  position: relative; }

.site-nav {
  float: right;
  line-height: 56px; }
  .site-nav .menu-icon {
    display: none; }
  .site-nav .page-link {
    color: #2a2a2a;
    line-height: 1.56; }
    .site-nav .page-link:not(:last-child) {
      margin-right: 20px; }
  @media screen and (max-width: 600px) {
    .site-nav {
      position: absolute;
      top: 9px;
      right: 1rem;
      background-color: #fafafa;
      border: 1px solid #cdcdcd;
      border-radius: 5px;
      text-align: right; }
      .site-nav .menu-icon {
        display: block;
        float: right;
        width: 36px;
        height: 26px;
        line-height: 0;
        padding-top: 10px;
        text-align: center; }
        .site-nav .menu-icon > svg {
          width: 18px;
          height: 15px; }
          .site-nav .menu-icon > svg path {
            fill: #484848; }
      .site-nav .trigger {
        clear: both;
        display: none; }
      .site-nav:hover .trigger {
        display: block;
        padding-bottom: 5px; }
      .site-nav .page-link {
        display: block;
        padding: 5px 10px;
        margin-left: 20px; }
        .site-nav .page-link:not(:last-child) {
          margin-right: 0; } }

/**
 * Site footer
 */
.site-footer {
  padding: 2rem 0;
  background: #1d1d1d; }

.footer-copyright {
  margin: 0;
  color: #f9f9f9;
  font-size: 14px;
  font-size: 0.77778rem; }

.footer-heading {
  font-size: 18px;
  margin-bottom: 1rem; }

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0; }

.footer-col-wrapper {
  font-size: 15px;
  color: #828282;
  margin-left: -1rem; }

.footer-col {
  float: left;
  margin-bottom: 1rem;
  padding-left: 1rem; }

.footer-col-1 {
  width: calc(35% - (2rem / 2)); }

.footer-col-2 {
  width: calc(20% - (2rem / 2)); }

@media screen and (max-width: 800px) {
  .footer-col-1,
  .footer-col-2 {
    width: calc(50% - (2rem / 2)); } }

@media screen and (max-width: 600px) {
  .footer-col {
    float: none;
    width: calc(100% - (2rem / 2)); } }

/**
 * Page content
 */
.page-content {
  padding: 2rem 0; }

.page-heading {
  font-size: 20px; }

.post-list {
  margin-left: 0;
  list-style: none; }
  .post-list > li {
    margin-bottom: 2rem; }

.post-meta {
  font-size: 15.75px;
  color: #828282; }

.post-link {
  display: block;
  font-size: 24px; }

/**
 * Posts
 */
.post-header {
  margin-bottom: 2rem; }

.post-title {
  font-size: 42px;
  letter-spacing: -1px;
  line-height: 1; }
  @media screen and (max-width: 800px) {
    .post-title {
      font-size: 36px; } }

.post-content {
  margin-bottom: 2rem; }
  .post-content h2 {
    font-size: 32px; }
    @media screen and (max-width: 800px) {
      .post-content h2 {
        font-size: 28px; } }
  .post-content h3 {
    font-size: 26px; }
    @media screen and (max-width: 800px) {
      .post-content h3 {
        font-size: 22px; } }
  .post-content h4 {
    font-size: 20px; }
    @media screen and (max-width: 800px) {
      .post-content h4 {
        font-size: 18px; } }

